<template>
  <div>
    <el-table
      :data="allMenuInfo"
      style="width: 100%;text-align: left">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item style="width: 100%">
              <template v-for="food in props.row.foods">
                <el-tag style="margin-left: 10px;">{{food.food_name}} | {{food.food_price}} 元</el-tag>
              </template>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        label="编号"
        prop="menu_id">
      </el-table-column>
      <el-table-column
        label="菜单名称"
        width="200px;"
        prop="menu_name"
      >
      </el-table-column>
      <el-table-column
        label="状态"
        >
        <template slot-scope="scope">
          <el-button
            size="mini"
            v-if="scope.row.menu_state == 1"
          >
            启用中
          </el-button>
          <el-button
            size="mini"
            v-if="scope.row.menu_state != 1"
            type="danger"
          >
            已停用
          </el-button>

        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        >
        <template slot-scope="scope">

          <el-button @click="toUpdate(scope.row)"
                     type="text" size="small">
            修改
          </el-button>

          <el-button type="text" size="small"
                     v-if="scope.row.menu_state == 1" @click="menustatelll('0',scope.row.menu_id)" >停用</el-button>

          <el-button type="text" size="small"
                     v-if="scope.row.menu_state != 1" @click="menustatelll('1',scope.row.menu_id)">启用</el-button>


        </template>
      </el-table-column>
    </el-table>

  </div>
</template>

<script>

  import {mapState, mapActions} from 'vuex';

  export default {
    name: "menu-list-info",

    data() {
      return {

      }
    },
    methods:{
      ...mapActions([
        'getAllMenuInfo','selMenuInfo','menustate'
      ])
      ,formatDate : function(ns) {
        var d = new Date(ns);
        var dformat = [ d.getFullYear(), d.getMonth() + 1, d.getDate() ].join('-')
          + ' ' + [ d.getHours(), d.getMinutes(), d.getSeconds() ].join(':');
        return dformat;
      },
      toUpdate(o){
        this.selMenuInfo({obj:o});
        this.$router.push({ name: 'updateMenu'})
      },
      menustatelll(state,id){
        console.log(id)
        var para = {};
        para.state = state;
        para.fail = this.fail;
        para.success = this.success;
        para.id = id;

        this.menustate(para);
      },
      success(){
        this.$notify({
          title: '成功',
          message: '修改成功',
          type: 'success'
        });
        this.getAllMenuInfo({});
        //this.form.name = "";
        //this.form.foodids = [];

      },
      fail(){
        this.$notify({
          title: '失败',
          message: '修改失败',
          type: 'error'
        });
      },
    }
    ,
    computed:{
      ...mapState({
        orderlistIng: state => state.mainpage.orderlistIng,
        allMenuInfo: state => state.mainpage.allMenuInfo
      }),
    },
    created(){

      //console.log(this.orderlistIng)
      this.getAllMenuInfo({});
    }
  }
</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



