import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/page/Index'
import addFood from '@/page/AddFood'
import AddMenu from '@/page/AddMenu'
import main1 from '@/page/Main'
import MenuInfo from '@/page/MenuInfo'
import UpdateMenu from '@/page/UpdateMenu'
import DeskInfo from '@/page/DeskInfo'


Vue.use(Router)

export default new Router({
  base: '/admin/',
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      children: [
        {
          path: '',
          redirect: '/main1'
        },
        {
          path: '/main1',
          name: 'main1',
          component: main1,
        }
        , {
          path: '/addfood',
          name: 'addfood',
          component: addFood,
        },
        {
          path: '/addmenu',
          name: 'addmenu',
          component: AddMenu,
        },
        {
          path: '/menuinfo',
          name: 'menuinfo',
          component: MenuInfo,
        },
        {
          path: '/updateMenu',
          name: 'updateMenu',
          component: UpdateMenu,
        },
        {
          path: '/deskInfo',
          name: 'deskInfo',
          component: DeskInfo,
        }
      ]
    }
  ]
})



