<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <span>新增菜品</span>
      </div>
      <div style="margin-bottom: 10px;">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="菜品名称:">
            <el-input v-model="form.name" style="width:60%;float:left"></el-input>
          </el-form-item>
          <el-form-item label="菜品价格:">
            <el-input v-model="form.price" style="width:200px;float:left"></el-input>
          </el-form-item>
          <el-form-item label="菜品图片:">
            <el-upload
              class="avatar-uploader"
              :action="imgFormAction"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload">
              <img v-if="imageUrl" :src="'/image/'.concat(imageUrl)" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="菜品描述:">
            <el-input type="textarea" v-model="form.desc"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">立即创建</el-button>
            <el-button>重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
  </div>
</template>

<script>
  const AddFoodImg = "/pioneer" + "/tools/upLoadPic";
  import {mapState, mapActions} from 'vuex';

  export default {
    name: "add-food",
    data() {
      return {
        imgFormAction: AddFoodImg,
        imageUrl: "",
        form: {
          name: '',
          price: '',
          desc: ''
        }
      }
    },
    methods: {
      ...mapActions([
        'addFood'
      ]),
      onSubmit() {
        //1. 校验参数
        if (this.form.name == "" || this.form.name.length < 1) {
          this.$notify({
            title: '失败',
            message: '菜品名称不能为空',
            type: 'error'
          });
          return;
        }
        if (this.form.price == "" || this.form.name.length < 1) {
          this.$notify({
            title: '失败',
            message: '菜品价格不能为空',
            type: 'error'
          });
          return;
        }
        if (this.imageUrl == "" || this.imageUrl.length < 1) {
          this.$notify({
            title: '失败',
            message: '请上传菜单图片',
            type: 'error'
          });
          return;
        }


        //2. 发送请求
        //this.$refs.upload.submit();
        var para = {};
        para.success = this.success;

        para.fail = this.fail;

        para.name = this.form.name;
        para.price = this.form.price;
        para.imageUrl = this.imageUrl;
        para.desc = this.form.desc;

        console.log(para);
        this.addFood(para);

        //3. 显示结果

      },
      handleAvatarSuccess(res, file) {
        /*this.imageUrl = URL.createObjectURL(file.raw);
        console.log(this.imageUrl);
        console.log(res.data.img);*/
        this.imageUrl = res.data.img;
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
        }
        return isJPG && isLt2M;
      },
      success() {
        this.$notify({
          title: '成功',
          message: '新增成功',
          type: 'success'
        });
        this.form.name = "";
        this.form.price = "";
        this.imageUrl = "";
        this.form.desc = "";
      },
      fail() {
        this.$notify({
          title: '失败',
          message: '新增失败',
          type: 'error'
        });
      }

    }
  }
</script>

<style scoped>

  .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



