<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <span>新增菜单</span>
      </div>
      <div style="margin-bottom: 10px;">
        <el-form ref="form" :model="form" label-width="80px">
          <el-form-item label="菜单名称:">
            <el-input v-model="form.name" style="width:60%;float:left"></el-input>
          </el-form-item>

          <el-form-item label="选择菜品:">
            <el-checkbox-group v-model="form.foodids">
              <template v-for="food in allfood">
                <el-checkbox :label="food.food_id" name="type" >{{food.food_name}}</el-checkbox>
              </template>
              <!--<el-checkbox label="美食/餐厅线上活动" name="type" value="" ></el-checkbox>-->
            </el-checkbox-group>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="onSubmit">立即创建</el-button>
            <el-button>重置</el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
  </div>
</template>

<script>
  const ADDFood = "http://localHost:9998/suning" + "/v1/addFood";
  import {mapState, mapActions} from 'vuex';

  export default {
    name: "add-food",
    data() {
      return {
        formAction: ADDFood,
        form: {
          name: '',
          foodids:[],
        }
      }
    },
    computed:{
      ...mapState({
        allfood:state => state.mainpage.allfood,
      })

    },
    methods: {
      ...mapActions([
        'queryAllFood','addMenu'
      ]),
      onSubmit() {
        //1. 校验参数
        if (this.form.name == "" || this.form.name.length < 1) {
          this.$notify({
            title: '失败',
            message: '菜单名称不能为空！',
            type: 'fail'
          });
          return;
        }
        console.log(this.form.foodids)
        if(this.form.foodids.length <1){
          this.$notify({
            title: '失败',
            message: '请选择菜品！',
            type: 'fail'
          });
          return;
        }
        //2. 发送请求
        var para = {};
        para.success = this.success;
        para.fail = this.fail;

        para.name = this.form.name;

        var foodids = "";
        for(var i = 0;i<this.form.foodids.length;i++){
          var id = this.form.foodids[i];
          foodids += id+":"
        }

        para.foodids = foodids.substr(0,foodids.length-1);

        console.log(para);
        this.addMenu(para);
        //3. 显示结果

      },
      success(){
        this.$notify({
          title: '成功',
          message: '新增成功',
          type: 'success'
        });
        this.form.name = "";
        this.form.foodids = [];

      },
      fail(){
        this.$notify({
          title: '失败',
          message: '新增失败',
          type: 'error'
        });
      }

    },created(){

      this.queryAllFood({});
    }
  }
</script>

<style scoped>

</style>



