<template>
  <div>
    <el-table
      :data="orderlistIng"
      style="width: 100%;text-align: left">
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label="桌位">
              <span>{{ props.row.desk.desk_name }}</span>
            </el-form-item>
            <el-form-item label="备注">
              <span>{{ props.row.order_desc }}</span>
            </el-form-item>
            <el-form-item style="width: 100%">
              <template v-for="food in props.row.foods">
                <el-tag style="margin-left: 10px;">{{food.food_name}} | {{food.food_price}} 元</el-tag>
              </template>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column
        label="订单号"
        prop="order_sno">
      </el-table-column>
      <el-table-column
        label="下单时间"
        width="200px;"
      >
        <template slot-scope="scope">
        {{formatDate(scope.row.order_date)}}
        </template>
      </el-table-column>
      <el-table-column
        label="金额"
        prop="order_amont">
      </el-table-column>
      <el-table-column
        label="当前状态"
        width="200px;"
        >
        <template slot-scope="scope">
          <el-button
            size="mini"
            v-if="scope.row.order_state == 1"
            type="danger"
            >
            {{scope.row.order_stateMsg}}
          </el-button>
          <el-button
            size="mini"
            v-if="scope.row.order_state != 1"
          >
            {{scope.row.order_stateMsg}}
          </el-button>
          <el-button
            size="mini"
            v-if="scope.row.order_paystate == 0"
            type="danger"
            >
            {{scope.row.order_paystateMsg}}
          </el-button>
          <el-button
            size="mini"
            v-if="scope.row.order_paystate != 0"
          >
            {{scope.row.order_paystateMsg}}
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        label="操作"
        >
        <template slot-scope="scope">

          <el-button @click="handleClick(scope.row)"
                     type="text" size="small"
                     v-if="scope.row.order_state == 1">
            菜上完
          </el-button>

          <el-button type="text" size="small"
                     v-if="scope.row.order_paystate == 0"
          >买单</el-button>

        </template>
      </el-table-column>
    </el-table>

  </div>
</template>

<script>

  import {mapState, mapActions} from 'vuex';

  export default {
    name: "order-list-info",

    data() {
      return {

      }
    },
    methods:{
      formatDate : function(ns) {
        var d = new Date(ns);
        var dformat = [ d.getFullYear(), d.getMonth() + 1, d.getDate() ].join('-')
          + ' ' + [ d.getHours(), d.getMinutes(), d.getSeconds() ].join(':');
        return dformat;
      }
    }
    ,
    computed:{
      ...mapState({
        orderlistIng: state => state.mainpage.orderlistIng
      }),
    },
    created(){
      console.log(this.orderlistIng)
    }
  }
</script>

<style>
  .demo-table-expand {
    font-size: 0;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



