<template>
  <div class="header" id="headerbar">
    <el-row>
      <el-col :span="8">
        <Logo></Logo>
      </el-col>

      <el-col :span="6">
        &nbsp;
      </el-col>
      <el-col :span="1">
      &nbsp;
      </el-col>
      <el-col :span="8">
        <el-menu class="meunonbutton el-menu-demo" mode="horizontal" @select="handleSelect">
          <el-menu-item index="5">首页</el-menu-item>
          <el-menu-item index="6">新增菜品</el-menu-item>
          <el-menu-item index="7">设置菜单</el-menu-item>
          <el-menu-item index="8">桌位管理</el-menu-item>
        </el-menu>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import Logo from '@/components/Logo'

  export default {
    name: "header-bar",
    methods: {
      handleSelect(key, keyPath) {
        //console.log(key);
        switch(key)
        {
          case '5':
            //console.log("in 5")
            this.$router.push({ name: 'main1'})
            break;
          case '6':
            //console.log("in 6")
            this.$router.push({ name: 'addfood'})
            //上传图片按钮
            //this.upLoadPic();
            break;
          case '7':
            //console.log("in 7")
            this.$router.push({ name: 'menuinfo'})
            break;
          case '8':
            //console.log("in 8")
            this.$router.push({ name: 'deskInfo'})
            break;
        }
      },
    },
    components:{
      Logo
    }
  }
</script>

<style>
  .meunonbutton{
    border-bottom:none;
  }
  .header{
    margin-top: 15px;
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #dcdfe6;
  }
</style>



