<template>
  <div>
    <el-card>
      <div slot="header" class="clearfix">
        <span>台桌</span>
      </div>
      <template v-for="deskinfo in deskInfos">
        <el-card style="height: 260px;width: 220px;float:left;margin-bottom: 20px;">
          <div><img height="200px;width:200px;" src=""></div>
          <div>桌号：{{deskinfo.deskName}}</div>
        </el-card>
      </template>
    </el-card>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex'

  export default {
    name: "desk-info",
    computed:{
      ...mapState({
        deskInfos:state => state.mainpage.deskInfo,
      }),
    },
    methods:{
      ...mapActions([
        'queryDesk',
      ])
    },
    created(){
      this.queryDesk({});
    }
  }
</script>

<style scoped>

</style>



