//import request from 'superagent'
//import jsonp from 'superagent-jsonp'

import axios from 'axios'
import jsonpAdapter from 'axios-jsonp'

import * as types from './api-type';
import * as urlPath from './res-path';

export default {

  //列表
  [types.QUERYORDERLISTING](para,callBack){
    var _url = urlPath.LOCALHOST + urlPath.QUERYORDERLISTING;
    var params = {};
    this.BASEJSONP(_url,params,callBack,para);
  },
  //新增菜品
  [types.ADDFOOD](para,callBack){
    var _url = urlPath.LOCALHOST + urlPath.ADDFOOD;
    console.log(_url);
    var params = {};

    var name = para.name
    var price = para.price
    var imageUrl = para.imageUrl
    var desc = para.desc
    //console.log(para);

    _url += "?";
    if(name){
      _url += "name="+name + "&"
    }
    if(price){
      _url += "price="+price + "&"
    }
    if(imageUrl){
      _url += "imageUrl="+imageUrl + "&"
    }
    if(desc){
      _url += "desc="+desc + "&"
    }


    this.BASEJSONP(_url,params,callBack,para);
  },
  [types.ALLFOOD](para,callBack){
    var _url = urlPath.LOCALHOST + urlPath.ALLFOOD;
    var params = {};
    this.BASEJSONP(_url,params,callBack,para);
  },
  [types.ADDMENU](para,callBack){
    var _url = urlPath.LOCALHOST + urlPath.ADDMENU;
    var params = {};

    var name = para.name
    var foodIds = para.foodids

    _url += "?";
    if(name){
      _url += "name="+name + "&"
    }
    if(foodIds){
      _url += "foodIds="+foodIds + "&"
    }

    this.BASEJSONP(_url,params,callBack,para);
  },
  [types.ALLMENUINFO](para,callBack){
    var _url = urlPath.LOCALHOST + urlPath.ALLMENUINFO;
    var params = {};
    params.where = "e";
    this.BASEJSONP(_url,params,callBack,para);
  },
  [types.UPDATEMENU](para,callBack){
    var _url = urlPath.LOCALHOST + urlPath.UPDATEMENU;
    var params = {};

    var name = para.name
    var foodIds = para.foodids
    var id = para.id

    _url += "?";
    if(name){
      _url += "name="+name + "&"
    }
    if(id){
      _url += "id="+id + "&"
    }
    if(foodIds){
      _url += "foodIds="+foodIds + "&"
    }

    this.BASEJSONP(_url,params,callBack,para);
  },
  [types.MENUSTATE](para,callBack){
    var _url = urlPath.LOCALHOST + urlPath.MENUSTATE;
    var params = {};
    params.id = para.id;
    params.state = para.state;

    this.BASEJSONP(_url,params,callBack,para);
  },
  [types.QUERYDESK](para,callBack){
    var _url = urlPath.LOCALHOST + urlPath.QUERYDESK;
    var params = {};

    this.BASEJSONP(_url,params,callBack,para);
  },


  /*-------------------------基础 jsonp 查询--------------------------------------*/
  [types.BASEJSONP](_url,params,callBack,para){
    axios({
      url: _url,
      params:params,
      adapter: jsonpAdapter
    })
      .then((res) => {
        //console.log(res.data);
        //var data = res.data;
        //console.log("return info is ["+data+"]");
        //console.log(data);
        para = para || {};
        para.res = res;
        callBack();
      });
  }

}



