<template>
  <div>
    <div>
      <!-- 订单列表 -->
      <el-card>
        <div slot="header" class="clearfix">
          <span>订单列表</span>
        </div>
        <div>
          <!-- left -->
          <div style="width: 20%;float: left">
            <el-card>
              <br>
              当前订单：{{ orderlistIng.length }} 单<br/>
              <hr>
              今日订单：{{ allOrderCount }} 单<br/>
            </el-card>
          </div>
          <!-- rigth -->
          <div style="width: 70%;float: left;margin-left: 30px;margin-bottom: 30px">
            <el-card style="height: 400px;overflow-y: scroll">
              <OrderListInfo></OrderListInfo>
            </el-card>
          </div>
        </div>
      </el-card>
    </div>
    <div>
      <!-- 桌位列表 -->
      <!--<el-card>
        <div slot="header" class="clearfix">
          <span></span>
        </div>

      </el-card>-->
    </div>
    <div>
      <!-- 菜单排名 -->
      <el-card>

      </el-card>
    </div>
  </div>
</template>

<script>

  import {mapState, mapActions} from 'vuex'
  import OrderListInfo from '@/components/OrderListInfo';


  export default {
    name: "mainpage",
    methods: {
      ...mapActions([
        'queryOrderListIng'
      ])
    },
    components:{
      OrderListInfo
    },
    computed:{
      ...mapState({
        allOrderCount:state => state.mainpage.allOrderCount,
        orderlistIng: state => state.mainpage.orderlistIng
      }),
    }
    ,
    created() {
      //加载制作中订单的数据
      this.queryOrderListIng({});
    }
  }
</script>

<style scoped>

</style>



