/**
 * main 操作对象
 *
 **/

import api from '../../api/document'
import * as types from '../mutation-types'


//状态
const state = {

  orderlistIng: [],  //正在制作中的订单列表信息
  allOrderCount:0,  //今日订单总数
  allfood:[],       //所有的食物
  allMenuInfo:[],       //所有菜单信息
  selMenu:{},
  deskInfo:[]

}

// getters
const getters = {

  orderlistIng: (state) => {
    return state.orderlistIng;
  },
  allOrderCount: (state) => {
    return state.allOrderCount;
  },
  allfood: (state) => {
    return state.allfood;
  },
  allMenuInfo: (state) => {
    return state.allMenuInfo;
  },
  selMenu: (state) => {
    return state.selMenu;
  },
  deskInfo: (state) => {
    return state.deskInfo;
  },
}

// actions
const actions = {

  queryOrderListIng({commit, state}, para) {
    api.QUERYORDERLISTING(para, () => {
      var data = para.res.data;
      if(data.code == "0"){
        //data.
        commit(types.QUERYORDERLISTING, data);
        //para.success(); //表示获取到了数据
      }
    })
  },
  addFood({commit, state}, para){
    api.ADDFOOD(para,() => {
      var data = para.res.data;
      if(data.code == "1"){
        para.success(); //成功
      }else{
        para.fail(); //失败
      }
    })
  },
  queryAllFood({commit, state}, para){

    api.ALLFOOD(para,() => {
      console.log(para);
      var data = para.res.data;
      if(data.code == "0"){
        commit(types.QUERYALLFOOD, data);
      }
    })
  },
  addMenu({commit, state}, para){
    api.ADDMENU(para,() => {
      var data = para.res.data;
      if(data.code == "1"){
        para.success(); //成功
      }else{
        para.fail(); //失败
      }
    })
  },
  getAllMenuInfo({commit, state}, para){
    api.ALLMENUINFO(para,() => {
      var data = para.res.data;
      if(data.code == "0"){
        commit(types.ALLMENUINFO, data);
      }
    })
  },
  selMenuInfo({commit, state}, para){
      var o = para.obj;
      commit(types.SELMENUINFO, o);

  },
  //修改菜单
  updateMenu({commit, state}, para){
    api.UPDATEMENU(para,() => {
      var data = para.res.data;
      if(data.code == "1"){
        para.success(); //成功
        //getAllMenuInfo();
      }else{
        para.fail(); //失败
      }
    })
  },
  //修改菜单
  menustate({commit, state}, para){
    api.MENUSTATE(para,() => {
      var data = para.res.data;
      if(data.code == "0"){
        para.success(); //成功
        //getAllMenuInfo();
      }else{
        para.fail(); //失败
      }
    })
  },
  //获取desk
  queryDesk({commit, state}, para){
    api.QUERYDESK(para,() => {
      var data = para.res.data;
      if(data.code == "1"){
        commit(types.QUERYDESK, data);
      }
    })
  },


}

// mutations
const mutations = {
  //查询首页信息
  [types.QUERYORDERLISTING](stata, data) { //加载热门图片

    var count = data.data.count;

    stata.allOrderCount = count.COUNT;
    stata.orderlistIng = new Array();

    var datas = data.data.lists;
    console.log(datas)
    //此处做操作将菜单合并
    var nowOrderInfo = {};

    for(var i = 0;i<datas.length;i++){
      var d = datas[i];
      //console.log(d)
      if(i == datas.length-1){
        var food = {};
        food.food_price = d.food_price;
        food.food_name = d.food_name;
        food.food_id = d.food_id;
        food.food_desc = d.food_desc;
        nowOrderInfo.foods.push(food);
        stata.orderlistIng.push(nowOrderInfo);
        console.log(nowOrderInfo)
        return;
      }
      if(i==0){//第一个
        //增加基本信息
        nowOrderInfo.order_amont = d.order_amont;       //金额
        nowOrderInfo.order_date = d.order_date; //下单时间
        nowOrderInfo.order_id = d.order_id; //订单id
        nowOrderInfo.order_desc = d.order_desc;       //订单描述
        nowOrderInfo.order_sno = d.order_sno;         //订单号
        nowOrderInfo.order_state = d.order_state;       //订单状态
        nowOrderInfo.order_paystate = d.order_paystate; //支付状态
        nowOrderInfo.foods = new Array();

        if(d.order_state == "1"){
          nowOrderInfo.order_stateMsg = "上菜中";
        }else if(d.order_state == "2"){
          nowOrderInfo.order_stateMsg = "菜已上完";
        }else{
          nowOrderInfo.order_stateMsg = "客户已评价";
        }

        if(d.order_paystate == "0"){
          nowOrderInfo.order_paystateMsg = "未结账";
        }else if(d.order_paystate == "1"){
          nowOrderInfo.order_paystateMsg = "已结账";
        }else{
          nowOrderInfo.order_paystateMsg = "";
        }


        var food = {};

        food.food_price = d.food_price;
        food.food_name = d.food_name;
        food.food_id = d.food_id;
        food.food_desc = d.food_desc;
        nowOrderInfo.foods.push(food);
        nowOrderInfo.desk = {};
        //console.log(food.food_name)
        nowOrderInfo.desk.desk_id = d.desk_id;
        nowOrderInfo.desk.desk_name = d.desk_name;
        nowOrderInfo.desk.desk_no = d.desk_no;
        nowOrderInfo.desk.desk_position = d.desk_position;
      }else{
        //判断是否同一单如果是则继续增加信息如果不是则初始化 nowOrderInfo
        if(nowOrderInfo.order_id == d.order_id){
          //增加菜单信息
          var food = {};
          food.food_price = d.food_price;
          food.food_name = d.food_name;
          food.food_id = d.food_id;
          food.food_desc = d.food_desc;
          nowOrderInfo.foods.push(food);
        }else{
          var OrderInfo = nowOrderInfo;
          //console.log(OrderInfo);
          stata.orderlistIng.push(OrderInfo);
          nowOrderInfo = new Array();
          //增加基本信息
          nowOrderInfo.order_amont = d.order_amont;       //金额
          nowOrderInfo.order_date = d.order_date; //下单时间
          nowOrderInfo.order_id = d.order_id; //订单id
          nowOrderInfo.order_desc = d.order_desc;       //订单描述
          nowOrderInfo.order_sno = d.order_sno;         //订单号
          nowOrderInfo.order_state = d.order_state;       //订单状态
          nowOrderInfo.order_paystate = d.order_paystate; //支付状态

          if(d.order_state == "1"){
            nowOrderInfo.order_stateMsg = "上菜中";
          }else if(d.order_state == "2"){
            nowOrderInfo.order_stateMsg = "菜已上完";
          }else{
            nowOrderInfo.order_stateMsg = "客户已评价";
          }

          if(d.order_paystate == "0"){
            nowOrderInfo.order_paystateMsg = "未结账";
          }else if(d.order_paystate == "1"){
            nowOrderInfo.order_paystateMsg = "已结账";
          }else{
            nowOrderInfo.order_paystateMsg = "";
          }

          nowOrderInfo.foods = new Array();
          var food = {};
          food.food_price = d.food_price;
          food.food_name = d.food_name;
          food.food_id = d.food_id;
          food.food_desc = d.food_desc;
          nowOrderInfo.foods.push(food);

          nowOrderInfo.desk = {};
          nowOrderInfo.desk.desk_id = d.desk_id;
          nowOrderInfo.desk.desk_name = d.desk_name;
          nowOrderInfo.desk.desk_no = d.desk_no;
          nowOrderInfo.desk.desk_position = d.desk_position;

        }
      }
    }
  },
  //加载所有食物
  [types.QUERYALLFOOD](stata, data) {
    //console.log(data);
    stata.allfood = data.data;
  },
  //加载所有菜单
  [types.ALLMENUINFO](stata, data) {
    //console.log(data);
    stata.allMenuInfo = data.data;
  },
  [types.SELMENUINFO](stata, o) {
    //console.log(o);
    stata.selMenu = o;
  },
  [types.QUERYDESK](stata, data) {
    console.log(data);
    stata.deskInfo = data.data;
  },


}

export default {
  state,
  getters,
  actions,
  mutations
}



